(function () {
  'use strict';

  angular
    .module('app.services')
    .factory('ToastService', ToastService);

  /** @ngInject */
  function ToastService($mdToast,toastr) {

    var service = {
      error: error,
      toastrError: toastrError,
      success: success,
      info : info
    };

    return service;

    function toastrError(text) {
      toastr.error(text, 'Error');

    }

    function success(text) {
      $mdToast.show(
        $mdToast.simple()
          .toastClass("toast-success")
          .action('Aceptar')
          .hideDelay(10000)
          .position('top right')
          .textContent(text)
      );
    }

    function info(text) {
       toastr.info(text, 'Informacin',{timeOut:2000});

    }

    function error(text) {
      $mdToast.show(
        $mdToast.simple()
          .toastClass("toast-error")
          .action('Aceptar')
          .hideDelay(10000)
          .position('top right')
          .textContent(text)
      );
    }
  }
}());
(function() {
    'use strict';

    angular
        .module('app.services')
        .factory('MQTTUtils', MQTTUtils);

    function MQTTUtils($rootScope, $timeout, $log,$q, HelperService, AlertService,_, MQTT, ORG, USERNAME, PASSWORD, WSBROKER, WSPORT) {
        var utils;
        var cliente;
        var reconnectTimeout = 1000;
        var UUID = createUuid();
        var watches = [];
        var State = {
            CONNECTED: "CONNECTED",
            DISCONNECTED: "DISCONNECTED"
        };
        var state = State.DISCONNECTED;

        utils = {
            connectClient: connectClient,
            addWatch: addWatch,
            onStartWatchDevices: onStartWatchDevices,
            onStartWatchDevice: onStartWatchDevice,
            onStopAllWatches: onStopAllWatches,
            send: send
        };

        return utils;

        function connectClient() {

            var mqttServer = WSBROKER;
            var mqttPort = WSPORT;
            var mqttCliente = "a:" + ORG + ":" + UUID;

            var options = {
                onSuccess: onConnect,
                cleanSession: true,
                useSSL: true,
                userName: USERNAME,
                password: PASSWORD,

                onFailure: function(message) {
                    $log.debug("Fail: ", message);
                    state = State.DISCONNECTED;
                    $timeout(connectClient, reconnectTimeout);
                }
            };
            cliente = new MQTT.Client(mqttServer, mqttPort, mqttCliente);
            cliente.onConnectionLost = onConnectionLost;
            cliente.onMessageArrived = onMessageArrived;
            cliente.connect(options);



        }



        function send(msg, qos) {
            var mqttMsg = new MQTT.Message(JSON.stringify(msg));
            mqttMsg.destinationName = destinationName;
            mqttMsg.qos = qos || 2;
            $log.debug('Send to ' + mqttMsg.destinationName + ", payload: " + JSON.stringify(msg));
            cliente.send(mqttMsg);


        }



        function onConnectionLost(responseObject) {
            state = State.DISCONNECTED;
            $timeout(function() {
                $log.debug("MQTT Client: Reconnecting...");
                connectClient();
            }, 100)
            if (responseObject.errorCode !== 0) {
                $log.debug("onConnectionLost:" + responseObject.errorMessage);
            }
        }



        function onConnect() {
            state = State.CONNECTED;
            $log.debug('Client onConnected');
            handlePendingSubscribes();

        }

        function onStartWatchDevices(devices) {
            for (var i = 0; i < devices.length; i++) {
                var topic = 'iot-2/type/' + devices[i].typeId + '/id/' + devices[i].deviceId + '/evt/6/fmt/json';
                if (!watches[topic] || watches[topic].length == 0) {
                    addWatch(topic);

                    if (state == State.CONNECTED) {
                        $log.debug("subscribe to " + topic);
                        cliente.subscribe(topic);
                    }
                }
            };
        }

        function onStartWatchDevice(device) {
            var topic = 'iot-2/type/' + device.typeId + '/id/' + device.deviceId + '/evt/6/fmt/json';
            if (!watches[topic] || watches[topic].length == 0) {
                addWatch(topic);

                if (state == State.CONNECTED) {
                    $log.debug("subscribe to " + topic);
                    cliente.subscribe(topic);
                }
            }
        }

        function handlePendingSubscribes() {
            var topics = watches;
            for (var i = 0; i < topics.length; i++) {
                var topic = topics[i];
                $log.debug("handle  subscribe to " + topic);
                cliente.subscribe(topic);
            }
        }

        function addWatch(topic) {
            if (watches.indexOf(topic) == -1) {
                watches.push(topic);
            }
        }


        function onStopAllWatches() {
            for (var i = 0; i < watches.length; i++) {
                var topic = watches[i];
                $log.debug("unsubscribing from " + topic);
                if (cliente && state == State.CONNECTED)
                    cliente.unsubscribe(topic);
            }
            watches = [];

        }

        function createUuid() {
            function S4() {
                return (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1);
            }
            return (S4() + S4() + '-' + S4() + '-4' + S4().substr(0, 3) + '-' + S4() + '-' + S4() + S4() + S4()).toLowerCase();
        }


        function onMessageArrived(message) {
            $log.debug(message);
            if (message && message.payloadString !== "") {
                var topic = message.destinationName;
                var typeId = topic.split("/")[2];
                var deviceId = topic.split("/")[4];
                var evtType = topic.split("/")[6];
                var payload = JSON.parse(message.payloadString).d;
                var timestamp = (new Date()).getTime();
                var dataOriginal = angular.copy(payload);
                var data = HelperService.addWeight(payload);
                var deviceEvent = {
                    deviceId: deviceId,
                    deviceType: typeId,
                    eventType: evtType,
                    data: data,
                    dataOriginal: dataOriginal,
                    timestamp: timestamp
                }

                $log.debug("onMessageArrived:", deviceEvent);
                 update(deviceEvent);
                 AlertService.checkAlert(deviceEvent);
            }
        }


        


        function update(data) {
            $rootScope.$broadcast('update', data);
        }

       
       
     

        




    }

})();
(function () {
  'use strict';

  angular
    .module('app.services')
    .factory('MareasService', MareasService);

  /** @ngInject */
  function MareasService($http,_ , moment) {
  	//var url='http://www.shoa.cl/mareas/mareas_p.php';
	// var headers = {
	// 			'Access-Control-Allow-Origin' : '*',
	// 			'Access-Control-Allow-Methods' : 'POST, GET, OPTIONS, PUT',
	// 			'Content-Type': 'application/x-www-form-urlencoded',
	// 			'Accept': 'application/json'
	// 		};
	var LIMIT_DAY=1;
	


	var service = {
      getMarea: getMarea,
      getPuertos: getPuertos
    };

    return service;


    function getMarea() {
   	 
   	var now= moment(); 

   	return $http.get('app/data/mareas/puertomontt.json').then(function(response) {
   	 	var mareas =[];
   	 	angular.forEach(response.data.mareas,function(marea) {
   	 		if(moment(marea.Campo1).diff(now, 'days') >=0  && moment(marea.Campo1).diff(now, 'days')<=LIMIT_DAY){
   	 			var date = moment(marea.Campo1);
   	 			if(marea.Campo2){
   	 				var parseTime= getTime(marea.Campo2);
   	 				var fullDate= date.add(parseTime.hour, 'hour').add(parseTime.minute, 'minute');
   	 				var data=marea.Campo3.split(' ');
   	 				mareas.push({fecha:fullDate.toDate().getTime(), value: data[0],symbol:data[1]});
   	 			}
   	 			if(marea.Campo4){
   	 				var parseTime= getTime(marea.Campo4);
   	 				var fullDate= date.add(parseTime.hour, 'hour').add(parseTime.minute, 'minute');
   	 				var data=marea.Campo5.split(' ');
   	 				mareas.push({fecha:fullDate.toDate().getTime(), value: data[0],symbol:data[1]});
   	 			}

   	 			if(marea.Campo6){
   	 				var parseTime= getTime(marea.Campo6);
   	 				var fullDate= date.add(parseTime.hour, 'hour').add(parseTime.minute, 'minute');
   	 				var data=marea.Campo7.split(' ');
   	 				mareas.push({fecha:fullDate.toDate().getTime(), value: data[0],symbol:data[1]});
   	 			}


   	 			if(marea.Campo8){
   	 				var parseTime= getTime(marea.Campo8);
   	 				var fullDate= date.add(parseTime.hour, 'hour').add(parseTime.minute, 'minute');
   	 				var data=marea.Campo9.split(' ');
   	 				mareas.push({fecha:fullDate.toDate().getTime(), value: data[0],symbol:data[1]});
   	 			}
   	 			
   	 			//var parseValue= {fecha:fullDate ,mediciones:[{hora:marea.Campo2,medicion:marea.Campo3},{hora:marea.Campo4,medicion:marea.Campo5},{hora:marea.Campo6,medicion:marea.Campo7},{hora:marea.Campo8,medicion:marea.Campo9}]}
   	 			//mareas.push(parseValue);
   	 		}

   	 	})

      
      
   	 
   	 	return  _.filter(mareas,function(marea) {
          if(now < moment(marea.fecha))
            return marea;
      });

   	 });

    }

    function getTime(time) {
    	var timeSplit= time.split(':')
    	var hour= timeSplit[0];
    	if(hour>12){
    		hour-=12;
    	}
    	var minute =timeSplit[1];
    	return {hour:hour,minute:minute};
    }

    function getPuertos() {
    	return puertosJson;
    }
    
  }
}());
(function() {
  'use strict';

  angular
    .module('app.services')
    .factory('managerDeviceService', managerDeviceService);

  function managerDeviceService(ApiUser, LoopBackAuth,ChartService, HelperService, Device, Event, Weather, $q, $log, _, Ruler) {
    var devicesCache;
    var devicesTypeCache;
    var rules;
    var alertsCache;

   
    var service = {
      getDevices: getDevices,
      getRules: getRules,
      addRule: addRule,
      getAlerts: getAlerts,
      addAlert: addAlert,
      deleteRule: deleteRule,
      newDeviceCache: newDeviceCache,
      register: register,
      update: update,
      unregister: unregister,
      getLastEvent: getLastEvent,
      refreshLastEvent: refreshLastEvent,
      getLocacion: getLocacion,
      updateLocation: updateLocation,
      reset: reset,
      getHistory: getHistory,
      count: count,
      getReports: getReports,
      getReportsFull: getReportsFull,
      getWeather: getWeather
    };

    return service;

    function setDevices(devices) {
      devicesCache = devices;
    }

    function setRules(rules) {
      rules = rules;
    }

    function newDeviceCache(device) {
      HelperService.changeIconMarker(device);
      devicesCache.push(device);
    }

    function deleteDeviceCache(device) {
      var i = _.findIndex(devicesCache, {
        deviceId: device.deviceId
      });
      if (i != -1)
        devicesCache.splice(i, 1);

    }

    function deleteRuleCache(rule) {
      var i = _.findIndex(rules, {
        id: rule.id
      });
      if (i != -1)
        rules.splice(i, 1);

    }

    function register(device) {
      return Device.register(device).$promise.then(function(response) {
        $log.debug('new register response', response);
        $log.debug('------------------');
        newDeviceCache(response);
        return response;
      });
    }


    function update(device) {
      return Device.update({deviceId:device.deviceId,typeId:device.typeId,deviceInfo:device.deviceInfo,name:device.name}).$promise.then(function(response) {
        $log.debug('update response', response);
        $log.debug('------------------');
        return response;
      });
    }



    function unregister(device) {
      return Device.unregister({deviceId:device.deviceId,typeId:device.typeId}).$promise.then(function(response) {
        $log.debug('unregister response', response);
        $log.debug('------------------');
        deleteDeviceCache(device);
        return response;
      });
    }



    function getLocacion(device) {
      if (!device.location) {
        return Device.location({deviceId:device.deviceId,typeId:device.typeId}).$promise.then(function(response) {
          device.location = response;
          $log.debug('Device location', response);
          $log.debug('------------------');
          return response;
        });
      }
      return $q.when(device.location);
    }


  function updateLocation(device) {
     
      return Device.updatelocation({deviceId:device.deviceId,typeId:device.typeId,location:device.location}).$promise.then(function(response) {
        $log.debug('Device new location', response);
        $log.debug('------------------');
        return response;
      });
    }

    function getDevices() {
      if (!devicesCache) {
        return ApiUser.devices({
          id: LoopBackAuth.currentUserId
        }).$promise.then(function(response) {
           var mapDevice = _.map(response, function(device) {
            HelperService.changeIconMarker(device);

            return device;
          })
          devicesCache = mapDevice;
          return devicesCache;
        });
      }

      return $q.when(devicesCache);


    }

    function getRules() {

      if (!rules) {
        return ApiUser.rules({
          id: LoopBackAuth.currentUserId
        }).$promise.then(function(response) {
          rules = response;
          return response;
        });
      }

      return $q.when(rules);

    }

    function addRule(rule) {
      rule.apiUserId = LoopBackAuth.currentUserId;
      return Ruler.replaceOrCreate(rule)
        .$promise.then(function(response) {
          var find = false;
          for (var i = 0; i < rules.length; i++) {
            if (rules[i].id === response.id) {
              rules[i] = response;
              find = true;
              break;
            }
          }
          if (!find)
            rules.push(response);
          return response;
        });
    }

    function getAlerts() {
      if (!alertsCache) {
        return ApiUser.alerts({
          id: LoopBackAuth.currentUserId
        }).$promise.then(function(response) {
          alertsCache = response;
          return response;
        });
      }

      return $q.when(alertsCache);
    }

    function addAlert(alert) {    
        alertsCache.unshift(alert);
    }



    function deleteRule(rule) {
      var rule = rule;
      return Ruler.deleteById({
        id: rule.id
      }).$promise.then(function(response) {
        deleteRuleCache(rule)
        return response;
      });
    }


    function getLastEvent(device) {
      if (!device.event) {
        return Device.cache({deviceId:device.deviceId,typeId:device.typeId}).$promise.then(function(response) {
          response.dataOriginal= angular.copy(response.data);
          response.data = HelperService.addWeight(response.data);
          HelperService.changeIconMarker(device);
          $log.debug('Device Cache', response);
          $log.debug('------------------');
          return response;
        })
      }
      return $q.when(device.event);

    }


    function refreshLastEvent(device) {
       return Device.cache({deviceId:device.deviceId,typeId:device.typeId}).$promise.then(function(response) {
          response.dataOriginal= angular.copy(response.data);
          response.data = HelperService.addWeight(response.data);
          HelperService.changeIconMarker(device);
          $log.debug('Device  refresh Cache', response);
          $log.debug('------------------');
          return response;
        })
    }



   


    function getHistory(device, limit, skip) {
      if (!device.history) {
        return Event.find({
          filter: {
            limit: limit || 100,
            skip: skip || 0,
            order: "timestamp DESC",
            where: {
              deviceId: device.deviceId
            },
            fields: {
              payload: true,
              timestamp: true
            }
          }
        }).$promise.then(function(response) {

          device.history = _.map(response, function(event) {
            return {
              data: HelperService.addWeight(event.payload),
              timestamp: event.timestamp
            };
          });
          $log.debug('History from device', device);
          $log.debug('------------------');
          return device;
        });
      }

      return $q.when(device);

    }

    function count(device) {
        return Device.events.count({
            id: device.deviceId
        }).$promise.then(function(response) {

          $log.debug('Count from device event', response.count);
          $log.debug('------------------');
          return response.count;
        });
    }

    function getReports(device, limit, skip) {

      return Device.events({id: device.deviceId,
        filter: {
          limit: limit || 100,
          skip: skip || 0,
          order: "timestamp DESC",
          fields: {
            payload: true,
            timestamp: true
          }
       } 
      }).$promise.then(function(response) {

        response = _.map(response, function(event) {
          return {
            data: HelperService.addWeight(event.payload),
            timestamp: event.timestamp
          };
        });
        $log.debug('Reports from device', device);
        $log.debug('------------------');
        return response;
      });



    }

    function getReportsFull(device) {

      return Device.events(
          {id: device.deviceId,
          filter: {
          order: "timestamp DESC",
          fields: {
            payload: true,
            timestamp: true
          }
        }
      }
      ).$promise.then(function(response) {

        response = _.map(response, function(event) {
          return {
            data: HelperService.addWeight(event.payload),
            timestamp: event.timestamp
          };
        });
        $log.debug('Reports full from device', device);
        $log.debug('------------------');
        return response;
      });



    }



    function getWeather(device) {
      if (device.location) {
        return Weather.today({
          latitude: device.location.latitude,
          longitude: device.location.longitude
        }).$promise.then(function(response) {
          $log.debug(response);
          return response;
        });
      }      

    }

    function reset() {
      devicesCache = null;
      devicesTypeCache = null;
      rules = null;
      alertsCache = null;
    }
  }
})();
