(function() {
	'use strict';


	angular
		.module('fuse')
		.filter('parseUnits', parseUnits)

	function parseUnits() {
		return function(input, units, onlySymbol) {
			if (!input)
				return input;

			for (var i = 0; i < units.length; i++) {
				if (units[i].id === input) {
					if (onlySymbol)
						return units[i].symbol;
					return units[i].name?units[i].name:units[i].id;
				}
			}
			if (onlySymbol)
				return '';

			return input;

		}
	}


})();
(function() {
	'use strict';


	angular
		.module('fuse')
		.filter('limitObjectTo', limitObjectTo)

	function limitObjectTo() {
		return function(obj, limit) {
			if(!obj)
				return[];

			var keys = Object.keys(obj);
			if (keys.length < 1) 
				return [];
			

			var ret = new Object,
				count = 0;
			angular.forEach(keys, function(key, arrayIndex) {
				if (count >= limit) {
					return false;
				}
				ret[key] = obj[key];
				count++;
			});
			return ret;
		};
	}


})();
(function() {
	'use strict';


	angular
		.module('fuse')
		.filter('colorState', colorState)

	function colorState(moment,_) {

		return function(input, hasAlert) {
			var RESET_TIME = 3600;
			var now = moment();
			var eventTime = moment(input);
			if (_.isNull(input))
				return 'gris';

			
			if(now.diff(eventTime, 'seconds') < RESET_TIME){
				return hasAlert?'rojo':'verde';
			}else{
				return 'gris';
			}

		}
	}


})();
(function() {
	'use strict';


	angular
		.module('fuse')
		.filter('capitalize', capitalize)

	function capitalize() {
		return function(input) {
			return (!!input) ? input.charAt(0).toUpperCase() + input.substr(1).toLowerCase() : '';
		}
	}


})();
(function() {
  'use strict';

  angular
    .module('fuse')
    .directive('serverValidation', serverValidation);

  function serverValidation() {
    return {
      restrict: "A",
      require: "ngModel",
      scope: {
        ngModel: "=",
        serverValidation: "=" // String or array of strings with name of errors
      },

      link: function(scope, elem, attr, ngModelCtrl) {
        function setValidity(errorName) {
          ngModelCtrl.$setValidity(errorName, true);
        }
        if (typeof(scope.serverValidation) == "string") {
          scope.arrServerValidation = [scope.serverValidation];
        } else {
          scope.arrServerValidation = scope.serverValidation;
        }
        var firstError = scope.arrServerValidation[0];
        scope.$watch('ngModel', function() {
          // workaround to don't update $setValidity, then changed value of ngModel
          // update $setValidity, only when server-error is true
          if (firstError && ngModelCtrl.$error[firstError])
            angular.forEach(scope.arrServerValidation, setValidity);
        });
      },
    }
  }
  

})();
(function ()
{
    'use strict';

    angular
        .module('app.core')
        .run(runBlock);

    /** @ngInject */
    function runBlock(msUtils, fuseGenerator, fuseConfig)
    {
        /**
         * Generate extra classes based on registered themes so we
         * can use same colors with non-angular-material elements
         */
        fuseGenerator.generate();

        /**
         * Disable md-ink-ripple effects on mobile
         * if 'disableMdInkRippleOnMobile' config enabled
         */
        if ( fuseConfig.getConfig('disableMdInkRippleOnMobile') && msUtils.isMobile() )
        {
            var bodyEl = angular.element('body');
            bodyEl.attr('md-no-ink', true);
        }

        /**
         * Put isMobile() to the html as a class
         */
        if ( msUtils.isMobile() )
        {
            angular.element('html').addClass('is-mobile');
        }

        /**
         * Put browser information to the html as a class
         */
        var browserInfo = msUtils.detectBrowser();
        if ( browserInfo )
        {
            var htmlClass = browserInfo.browser + ' ' + browserInfo.version + ' ' + browserInfo.os;
            angular.element('html').addClass(htmlClass);
        }
    }
})();
(function ()
{
    'use strict';

    angular
        .module('app.core')
        .config(config);

    /** @ngInject */
    function config($ariaProvider, $logProvider, msScrollConfigProvider, fuseConfigProvider)
    {
        // Enable debug logging
        $logProvider.debugEnabled(true);

        /*eslint-disable */

        // ng-aria configuration
        $ariaProvider.config({
            tabindex: false
        });

        // Fuse theme configurations
        fuseConfigProvider.config({
            'disableCustomScrollbars'        : false,
            'disableCustomScrollbarsOnMobile': true,
            'disableMdInkRippleOnMobile'     : true
        });

        // msScroll configuration
        msScrollConfigProvider.config({
            wheelPropagation: true
        });

        /*eslint-enable */
    }
})();
(function ()
{
    'use strict';

    angular
        .module('fuse')
        .run(runBlock);

    /** @ngInject */
    function runBlock($rootScope, $timeout, $state,$location,amMoment,authService,MQTTUtils)
    {


        //Change locate moment 
        amMoment.changeLocale('es');

        //Activete MQTT client
        MQTTUtils.connectClient();

        // Activate loading indicator
        var stateChangeStartEvent = $rootScope.$on('$stateChangeStart', function ()
        {
            $rootScope.loadingProgress = true;
        });

        // De-activate loading indicator
        var stateChangeSuccessEvent = $rootScope.$on('$stateChangeSuccess', function ()
        {
            $timeout(function ()
            {
                $rootScope.loadingProgress = false;
            });
        });

        // Store state in the root scope for easy access
        $rootScope.state = $state;

        // Cleanup
        $rootScope.$on('$destroy', function ()
        {
            stateChangeStartEvent();
            stateChangeSuccessEvent();
        });


        var restrictedPage = $.inArray($location.path(), ['/','/register','/reset']) === -1;
        $rootScope.$on('$locationChangeStart', function (event, next, current) {
            if (restrictedPage && !authService.isAuthenticated()) {
                $location.path('/');
            } 
            if ($location.path()=='/' && authService.isAuthenticated()) {
                $location.path('/home');
            }            
        });
    }
})();
(function ()
{
    'use strict';

    angular
        .module('fuse')
        .config(routeConfig);

    /** @ngInject */
    function routeConfig($stateProvider, $urlRouterProvider, $locationProvider)
    {
    
      $locationProvider.html5Mode(true);

      $urlRouterProvider.otherwise(function($injector, $location) {
          var $state = $injector.get("$state");
          $state.go('app.pages.home');
        });

       

        // State definitions
        $stateProvider
            .state('app', {
                abstract: true,
                views   : {
                    'main@'         : {
                        templateUrl: 'app/core/layouts/vertical-navigation.html',
                        controller : 'MainController as vm'
                    },
                    'toolbar@app'   : {
                        templateUrl: 'app/toolbar/layouts/vertical-navigation/toolbar.html',
                        controller : 'ToolbarController as vm'
                    },
                    'navigation@app': {
                        templateUrl: 'app/navigation/layouts/vertical-navigation/navigation.html',
                        controller : 'NavigationController as vm'
                    }
                }
            });

        
    }

})();

